import { useContext,useEffect } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import prev from '../../assets/icons/prev.svg'
import next from '../../assets/icons/next.svg'
import './Nav.css'
const Nav =()=>{
    const state = useContext(DemocracyStateContext)
    const dispatch = useContext(DemocracyDispatchContext)
    const { scenes,currentStory,contentElt } = state
    const handleNavigate = e => {
      dispatch({type:'NAVIGATE',payload:e.target.getAttribute('scene')})
      setTimeout(()=>dispatch({type:'ENABLE_NAVIGATE'}),3000)
    }
    let href = '#Shadow'
    useEffect(()=>{
      if(contentElt){
        if(currentStory===15 || currentStory===0){
          const top = currentStory===15 ? (window.innerHeight*2)*(currentStory+1) : window.innerHeight+100
          window.scrollTo({
            top,
            left: 0,
            behavior: 'smooth'
          })
        }
      }
    },[contentElt,currentStory])
  return(
    <main className="Nav">
     <nav>
        <ul onClick={handleNavigate}>
          {/*<li><a href="#ws">{currentStory}</a></li>*/}
          <li className={currentStory===0?'selected':null}><a scene={0} href="#Intro">Back to Top</a></li>
          <li className={currentStory>=1 && currentStory<=5?'selected':null}><a scene={1} href="#NetworkTitle">Network of collaborators</a></li>
          <li className={currentStory>=6 && currentStory<=9?'selected':null}><a scene={6} href="#History">History lesson from 2016</a></li>
          <li className={currentStory>=10 && currentStory<=14?'selected':null}><a scene={10} href="#History">Responding to the threat</a></li>
          <li className={currentStory>14?'selected':null}><a scene={15} href="#Static">An election like no other</a></li>
          <li className="btn">
            <a className={currentStory===0?'disabled':null} scene={currentStory - 1} href={currentStory === 0 ? '#Intro' : href}><img scene={currentStory - 1}  width="18" src={prev} alt="prev"/></a>
            <a className={currentStory===scenes.length-1?'disabled':null} scene={currentStory + 1} href={currentStory === 15 ? '#Static' : href}><img scene={currentStory + 1}  width="18" src={next} alt="next"/></a>
          </li>
        </ul>
     </nav>
   </main>
  )
}

export default Nav
