import './MNetwork.css'
import LottieAnim from '../LottieAnim/'
import mavatar from '../../assets/lottie/mavatar.json'
const MNetwork =()=>(
  <main className="MNetwork mobilePar">
    <h2>Building a network, one connection at a time</h2>
      <p>Planning for the 2020 U.S. election began in earnest at Microsoft in December 2019, with around 140 people ultimately joining the Microsoft Elections team.</p>
      <LottieAnim
      playAnim={true}
      looped={true}
      data={mavatar}/>
      <p>There were staff from the Azure and Office engineering teams, high-priority product support personnel, customer liaison specialists, and a significant representation from the Detection and Response Team. As for so many people across not just America but the world, the pandemic meant those team members were working from home.</p>
      <p>Connections were created to colleagues at Bing and LinkedIn, to facilitate the tracking of disinformation and fake profiles. It was also possible to monitor and respond to comments in Xbox chat, thanks to the involvement of that team. Additionally, connections were put in place with all 50 states and other major figures in the information space, such as Facebook, Google, Twitter, and others.</p>
      <p>These detailed and careful preparations also reflected some of the lessons learned during previous rounds of elections. One in particular had become a watchword for those advocating a careful and considered approach.</p>

  </main>
)

export default MNetwork
