import Lottie from "react-lottie"
import './LottieAnim.css'

const LottieAnim =({data,playAnim,looped})=>{

  const lottieOptions = {
    loop: looped,
    autoplay: true,
    animationData: data,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice"
    }
  }
  return <div className={playAnim?'LottieAnim play':'LottieAnim'}><Lottie isStopped={!playAnim} options={lottieOptions}/></div>
}
export default LottieAnim
