import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Persistent.css'
const Persistent =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="Persistent">
       <article className="bigL">
         <h2 style={{"transform":"translate3d(-"+(currentStory === 10 ? 0 : 40)+"vw,0,0)"}}>
           Persistent
           vigilance and
           rapid response
         </h2>
       </article>
    </section>
  )
}

export default Persistent
