import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './History.css'
const History =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="History">
       <article className="bigL">
         <h2 style={{"transform":"translate3d(-"+(currentStory === 6 ? 0 : 40)+"vw,0,0)"}}>
            A history
            lesson from
            2016
         </h2>
       </article>
    </section>
  )
}

export default History
