import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Author.css'
const Author =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state

    const author = currentStory===12 ||
                     currentStory===13
  return(
    <aside className={`Author ${author?'go':''}`}>
       <strong>Ethan Chumley,</strong>
       <span>Senior Cybersecurity Strategist,</span>
       <span>Microsoft Defending Democracy Program</span>
    </aside>
  )
}

export default Author
