import { useContext } from 'react'
import NetworkTitle from '../NetworkTitle'
import NetworkContent from '../NetworkContent'
import Connections from '../Connections'
import Preperations from '../Preperations'
import History from '../History'
import HistoryContent from '../HistoryContent'
import Politics from '../Politics'
import Shadow from '../Shadow'
import Persistent from '../Persistent'
import PersistentContent from '../PersistentContent'
import DarkWeb from '../DarkWeb'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Content.css'
const Content =()=>{
  const state = useContext(DemocracyStateContext)
  const { currentStory,scenes} = state
  return(
    <main
    className="Content"
    style={{
        "width":(scenes.length*100)+"vw",
        "transform":"translate3d(-"+(currentStory*100)+"vw,0,0)"
      }}>
      <section/>{/*Intro*/}
      <NetworkTitle/>
      <NetworkContent/>
      <section/>{/*Scanner_one*/}
      <Connections />
      <Preperations />
      <History />
      <HistoryContent />
      <Politics />
      <Shadow />
      <Persistent />
      <PersistentContent />
      <section/>{/*Ethan1*/}
      <section/>{/*Ethan2*/}
      <section/>{/*Conclusion*/}
      <section/>{/*Static*/}
    </main>
  )
}

export default Content
