import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './PersistentContent.css'
const PersistentContent =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="PersistentContent">
       <article className="smallFL">
         <div style={{"transform":"translate3d(-"+(currentStory === 11 ? 0 : 40)+"vw,0,0)"}}>
          <p>Tales of the dark web routinely catch the public’s attention. It’s a strange and seemingly sinister place. It was there that the login credentials of an election official from one state were found for sale.</p>
        </div>
       </article>
    </section>
  )
}

export default PersistentContent
