import { useContext } from 'react'
import './MHeader.css'
import scroll from '../../assets/lottie/scroll.json'
import LottieAnim from '../LottieAnim/'
import Video from '../Video/'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
const MHeader =()=>{
  const dispatch = useContext(DemocracyDispatchContext)
  return (
    <main className="MHeader">
      <header>
        <Video/>
      </header>
      <section>
        <button
        role="navigation"
        className="accessible"
        onClick={()=>dispatch({type:'MAKE_ACCESSIBLE'})}>Click here for interactive version</button>
        <h1>Keeping your vote safe and secure – an inside story from the 2020 election</h1>
        <p>While the eyes of the world were fixed on the 2020 U.S. election, behind the scenes, public- and private-sector organizations of all sizes mobilized teams of engineers, advisors and cybersecurity professionals. </p>
        <p>States like Idaho and Ohio found themselves having to adjust to the challenges of running elections during the pandemic. </p>
        <p>Groups including the Brennan Center for Justice and federal government agencies such as the Cybersecurity and Infrastructure Security Agency (CISA) devised trainings that drew upon their experience.</p>
        <p>Technology vendors like BPro supported hundreds of county election officials nationwide through this intense period.</p>
        <p>Their mission? To safeguard the integrity of the election by defending against the many trips, traps and tribulations that had potential to cause problems.</p>
        <p>This is the story of how this diverse group adapted to a new landscape of cybercrime and nation-state attacks laid bare by the 2016 election. </p>
        <p>It’s the story of how they avoided unforced technical errors and strengthened their processes against the backdrop of the COVID-19 pandemic, which added new layers of pressure and complexity.</p>
        <p>It’s the story of the depth, detail and duration of the planning and preparation they undertook, which was fundamental to their ultimate success.</p>
      </section>
      <div className="scrollCtn">
        <a href="#NetworkTitle"><LottieAnim playAnim={true} looped={true} data={scroll}/></a>
      </div>
    </main>
  )
}

export default MHeader
