import { useContext,useEffect,useRef } from 'react'
import './Header.css'
import scroll from '../../assets/lottie/scroll.json'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import Video from '../Video/'
import LottieAnim from '../LottieAnim/'
const Header =()=>{
  const dispatch = useContext(DemocracyDispatchContext)
  const HeaderRef = useRef(null)

  useEffect(() => {
    const headerRefObserver = new IntersectionObserver( ([{ isIntersecting }]) => {
      dispatch({type:'HERO_IS_HERE',payload:isIntersecting})
    },{threshold:[0]})


    headerRefObserver.observe(HeaderRef.current)
    return ()=> headerRefObserver.disconnect()
  }, [])


  return (
    <main ref={HeaderRef} className="Header">
      <header>
        <Video/>
      </header>
      <section>
        <button
        role="navigation"
        className="accessible"
        onClick={()=>dispatch({type:'MAKE_ACCESSIBLE'})}>Click here for accessible version</button>
        <h1>Keeping your vote safe and secure – an inside story from the 2020 election</h1>
        <p>In November 2020, the eyes of the world were fixed on the outcome of the U.S. presidential election. But in the months leading up to that point, teams of engineers, advisors and cybersecurity professionals from the public- and private-sectors were hard at work behind the scenes to ensure it all went smoothly.</p>
        <p>States like Idaho and Ohio found themselves having to adjust to the challenges of running elections during the pandemic.</p>
        <p>Groups including the Brennan Center for Justice and federal government agencies such as the Cybersecurity and Infrastructure Security Agency devised trainings that drew upon their experience.</p>
        <p>Technology vendors like BPro supported hundreds of county election officials nationwide through this intense period.</p>
        <p>Their mission? To safeguard the integrity of the election by defending against the many trips, traps and tribulations that had potential to cause problems.</p>
        <p>This is the story of how this diverse group adapted to a new landscape of cybercrime and nation-state attacks laid bare by the 2016 election. </p>
        <p>It’s the story of how they avoided unforced technical errors and strengthened their processes against the backdrop of the Covid-19 pandemic that added new layers of pressure and complexity.</p>
        <p>It’s also the story of the depth, detail and duration of the planning and preparation they undertook, which was fundamental to their eventual success.</p>
      </section>
      <div className="scrollCtn">
        <a href="#NetworkTitle"><LottieAnim playAnim={true} looped={true} data={scroll}/></a>
      </div>
    </main>
  )
}

export default Header
