import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Preperations.css'
const Preperations =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="Preperations">
       <article className="smallL">
         <div style={{"transform":"translate3d(-"+(currentStory === 5 ? 0 : 40)+"vw,0,0)"}}>
            <p>These preparations reflected some of the lessons learned during previous rounds of elections. One lesson in particular became a watchword for those advocating a careful and considered approach.</p>
        </div>
       </article>
    </section>
  )
}

export default Preperations
