import './MDarkweb.css'
import LottieAnim from '../LottieAnim/'
import mdarkweb from '../../assets/lottie/mdarkweb.json'
const MDarkweb =()=>(
  <main className="MDarkweb mobilePar">
    <h2>Persistent vigilance and rapid response </h2>
    <p>Tales of the dark web routinely catch the public’s attention. It’s a strange and seemingly sinister place. It was there that the login credentials of a senior election official were found for sale just a few days before polling day.</p>
    <LottieAnim
    playAnim={true}
    looped={true}
    data={mdarkweb}/>
    <p><span role="quote">Someone on the identity team alerted me to the situation,</span> recalls Ethan Chumley, Senior Cybersecurity Strategist for the Microsoft Defending Democracy Program. “Within an hour, we’d contacted the state’s chief information officer, who reset the username and password in question immediately.”</p>
    <blockquote>“It was an hour’s worth of work and then it was done. But had it not been resolved, things might have been very different.”</blockquote>
    <p>To cybercriminals, usernames and passwords are like the keys to the castle. Once inside a corporate network they can start to move around freely, embedding and hiding their malicious software.</p>
    <p>The dangers posed by hackers infiltrating a single point on the network are myriad. They might steal valuable data or upload ransomware. They might shut down vital services. Or they might simply be seeking attention by causing trouble.</p>
  </main>
)

export default MDarkweb
