import { useContext,useState,useEffect,useRef } from 'react'
import { Waypoint } from 'react-waypoint'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import './Story.css'

const Story =()=>{
  const dispatch = useContext(DemocracyDispatchContext)
  const sceneOne = useRef(null)
  useEffect(()=>dispatch({type:'SET_SCENE_REF',payload:sceneOne.current.getBoundingClientRect().y}),[])
  return(
    <main
      ref={sceneOne}
      id="Story"
      className="Story">
      <div style={{"height":"200vh"}}/>
      <Waypoint
        onEnter={()=>dispatch({type:'SET_SCALE_TRIANGLE',payload:true})}
        onLeave={()=>dispatch({type:'SET_SCALE_TRIANGLE',payload:false})}>
        <section>
          <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:1})}>
          <p id="NetworkTitle" style={{"height":"200vh"}}>NetworkTitle</p>
        </Waypoint>

          <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:2})}>
            <p id="NetworkContent" style={{"height":"200vh"}}>NetworkContent</p>
          </Waypoint>

        </section>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:3})}>
        <p id="Scanner_one" style={{"height":"200vh"}}>Scanner_one</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:4})}>
        <p id="Connections" style={{"height":"200vh"}}>Connections</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:5})}>
        <p id="Preperations" style={{"height":"200vh"}}>Preperations</p>
      </Waypoint>

      <Waypoint
        onEnter={()=>{
          dispatch({type:'SET_SCALE_TRIANGLE',payload:true})
          dispatch({type:'SET_STORY',payload:6})}}
        onLeave={()=>dispatch({type:'SET_SCALE_TRIANGLE',payload:false})}>
        <p id="History" style={{"height":"200vh"}}>History</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:7})}>
        <p id="HistoryContent" style={{"height":"200vh"}}>HistoryContent</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:8})}>
        <p id="Politics" style={{"height":"200vh"}}>Politics</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:9})}>
        <p id="Shadow" style={{"height":"200vh"}}>Shadow</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:10})}>
        <p id="Persistent" style={{"height":"200vh"}}>Persistent</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:11})}>
        <p id="PersistentContent" style={{"height":"200vh"}}>PersistentContent</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:12})}>
        <p id="Ethan_one" style={{"height":"200vh"}}>Ethan_one</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:13})}>
        <p id="Ethan_two" style={{"height":"200vh"}}>Ethan_two</p>
      </Waypoint>

      <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:14})}>
        <p id="Conclusion" style={{"height":"200vh"}}>Conclusion</p>
      </Waypoint>
    </main>
  )
}

export default Story
