import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Connections.css'
const Connections =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="Connections">
       <article className="smallRBottom">
         <div style={{"transform":"translate3d(-"+(currentStory === 4 ? 0 : 40)+"vw,0,0)"}}>
            <p>Connections were in place with all 50 states and other major figures in the information space, including but not just, Google, Facebook and Twitter.</p>
        </div>
       </article>
    </section>
  )
}

export default Connections
