import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './Politics.css'
const Politics =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="Politics">
       <article className="smallL long">
         <div style={{"transform":"translate3d(-"+(currentStory === 8 ? 0 : 40)+"vw,0,0)"}}>
            <p>2016 saw U.S. politics upended in an historic way. The Democratic National Committee and people who were part of Hillary Clinton's presidential campaign were the target of a major hack-and-leak operation. Later investigations uncovered an extensive campaign, targeting not just political campaigns but also state election infrastructure. The hack has since been attributed by U.S. intelligence to Russian state actors.</p>
        </div>
       </article>
    </section>
  )
}

export default Politics
