import { useContext } from 'react'
import './Mobile.css'
import MHeader from '../MHeader/'
import MNetwork from '../MNetwork/'
import MHistory from '../MHistory/'
import MDarkweb from '../MDarkweb/'
import StaticContent from '../StaticContent/'
import Footer from '../Footer/'
import DemocracyStateContext from '../../context/DemocracyStateContext'
const Mobile =()=>{
  const state = useContext(DemocracyStateContext)
  return (
    <main className={`Mobile ${!state.accessible ? 'nonAccessible':''}`}>
      <MHeader/>
      <MNetwork/>
      <MHistory/>
      <MDarkweb/>
      <StaticContent/>
      <Footer/>
    </main>
  )
}

export default Mobile
