import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './NetworkContent.css'
const NetworkContent =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="NetworkContent">
       <article className="smallL">
         <div style={{"transform":"translate3d(-"+(currentStory === 2 ? 0 : 40)+"vw,0,0)"}}>
          <p>Heightened planning for the 2020 U.S. election kicked in at Microsoft in December 2019, with a cross-company group that eventually grew to 140 people being assembled for the Microsoft Elections team.</p>
          <p>There were staff from the Azure and Office engineering teams, high-priority product support personnel, customer service specialists, and significant representation from the Detection and Response Team. As 2020 and pandemic realties set in, those team members were working from home.</p>
          <p>Connections were created with colleagues at Bing and LinkedIn, to facilitate the tracking of disinformation and fake user profiles. It was also possible to monitor and respond to comments in Xbox chat, because of  the involvement of that team.</p>
        </div>
       </article>
    </section>
  )
}

export default NetworkContent
