import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './HistoryContent.css'
const HistoryContent =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="HistoryContent">
       <article className="smallR">
         <div style={{"transform":"translate3d(-"+(currentStory === 7 ? 0 : 40)+"vw,0,0)"}}>
            <p>In the summer of 2016, a hacking group calling itself the Shadow Brokers stunned the cybersecurity community. It began releasing files that appeared to have been stolen from the National Security Agency</p>
            <p>Among those files were tools NSA operatives use to conduct covert cyber operations. This hack  was an act that set off a chain of events that would have repercussions for years to come.</p>
        </div>
       </article>
    </section>
  )
}

export default HistoryContent
