import { useEffect,useRef,useContext } from 'react'
import vote from '../../assets/videos/vote.mp4'
import poster from '../../assets/images/vote.jpg'
import DemocracyStateContext from '../../context/DemocracyStateContext'
const Video =()=>{
    const state = useContext(DemocracyStateContext)
    const { accessible } = state
    const videoRef = useRef(null)
    useEffect(()=>videoRef.current.play(),[accessible])
  return(
    <video
      ref={videoRef}
      poster={poster}
      src={vote}
      autoPlay muted
      role="presentation"/>
  )
}

export default Video
