let initialDemocracyState = {
  scroll:0,
  percent:0,
  sceneOne:null,
  contentElt:null,
  direction:null,
  isHero:true,
  isScrollable:true,
  enableNavigate:true,
  passValue:'',
  currentStory:-1,
  scaleTriangle:false,
  accessible:false,
  mobile:window.innerWidth<=1024,
  scenes:[
    'Intro',
    'NetworkTitle',
    'NetworkContent',
    'Scanner_one',
    'Connections',
    'Preperations',
    'History',
    'HistoryContent',
    'Politics',
    'Shadow',
    'Persistent',
    'PersistentContent',
    'Ethan_one',
    'Ethan_two',
    'Conclusion',
    'Static'
  ]
}

const democracyReducer = (state, action) => {
  switch (action.type) {

    case 'SET_CONTENT_ELT':
      let SET_CONTENT_ELT = {...state}
      SET_CONTENT_ELT.contentElt = action.payload
    return SET_CONTENT_ELT;

    case 'STATIC_IS_HERE':
      let STATIC_IS_HERE = {...state}
      STATIC_IS_HERE.isStatic = action.payload
      if(!STATIC_IS_HERE.isHero){
        //if(action.payload) STATIC_IS_HERE.currentStory = 15
      }
    return STATIC_IS_HERE;

    case 'HERO_IS_HERE':
      let HERO_IS_HERE = {...state}
      HERO_IS_HERE.isHero = action.payload
    return HERO_IS_HERE;

    case 'ENABLE_NAVIGATE':
      let ENABLE_NAVIGATE = {...state}
      ENABLE_NAVIGATE.enableNavigate = true
    return ENABLE_NAVIGATE;

    case 'NAVIGATE':
      let NAVIGATE = {...state}
      NAVIGATE.currentStory = Number(action.payload)
      NAVIGATE.enableNavigate = false
    return NAVIGATE;

    case 'SET_SCENE_REF':
      let SET_SCENE_REF = {...state}
      SET_SCENE_REF.sceneOne = action.payload
    return SET_SCENE_REF;

    case 'RESET_COUNT':
      let RESET_COUNT = {...state}
      RESET_COUNT.currentStory = 0
    return RESET_COUNT;

    case 'START_EXPERIENCE':
      let START_EXPERIENCE = {...state}
      //START_EXPERIENCE.currentStory = 1

    return START_EXPERIENCE;

    case 'SET_SCROLLABLE':
      let SET_SCROLLABLE = {...state}
      if (!SET_SCROLLABLE.isHero && !SET_SCROLLABLE.isStatic) {

        if (SET_SCROLLABLE.enableNavigate) {
          if (SET_SCROLLABLE.currentStory <= 15) {
            if (SET_SCROLLABLE.direction) SET_SCROLLABLE.currentStory = SET_SCROLLABLE.currentStory - 1
            else SET_SCROLLABLE.currentStory = SET_SCROLLABLE.currentStory + 1
          }
          if(SET_SCROLLABLE.currentStory === -1) SET_SCROLLABLE.currentStory = 0
        }
      }

    return SET_SCROLLABLE;

    case 'SET_PASSWORD':
      let SET_PASSWORD = {...state}
      SET_PASSWORD.passValue = action.payload
    return SET_PASSWORD;

    case 'MAKE_ACCESSIBLE':
      let MAKE_ACCESSIBLE = {...state}
      MAKE_ACCESSIBLE.accessible = !MAKE_ACCESSIBLE.accessible
      if (!MAKE_ACCESSIBLE.accessible) MAKE_ACCESSIBLE.currentStory = 0
    return MAKE_ACCESSIBLE;

    case 'SET_MOBILE':
      let SET_MOBILE = {...state}
      SET_MOBILE.mobile = action.payload
    return SET_MOBILE;

    case 'SET_SCALE_TRIANGLE':
      let SET_SCALE_TRIANGLE = {...state}
      SET_SCALE_TRIANGLE.scaleTriangle = action.payload
    return SET_SCALE_TRIANGLE;

    case 'SET_STORY':
      let SET_STORY = {...state}
      //SET_STORY.currentStory = action.payload
    return SET_STORY;

    case 'SET_DIRECTION':
      let SET_DIRECTION = {...state}
      SET_DIRECTION.direction = action.payload
    return SET_DIRECTION;

    case 'SET_SCROLL':
      let SET_SCROLL = {...state}
      if(SET_SCROLL.scroll !== action.payload)SET_SCROLL.scroll = action.payload
      if(SET_SCROLL.isScrollable) SET_SCROLL.isScrollable = false
    return SET_SCROLL;

    case 'SET_PERCENT':
      let SET_PERCENT = {...state}
      if(SET_PERCENT.percent !== action.payload)SET_PERCENT.percent = action.payload
    return SET_PERCENT;


    default: throw new Error('Unexpected action');
  }
}

export default democracyReducer
export { initialDemocracyState }
