import './MHistory.css'
import LottieAnim from '../LottieAnim/'
import mshadow from '../../assets/lottie/mshadow.json'
const MHistory =()=>(
  <main className="MHistory mobilePar">
    <h2>A history lesson from 2016 </h2>
      <p>In the summer of 2016, a hacking group calling itself the Shadow Brokers stunned the cybersecurity community. It began releasing files that appeared to have been stolen from the National Security Agency (NSA).</p>
      <p>Among those files were some of the tools NSA operatives use to conduct covert cyber operations. This set off a chain of events that would have repercussions for years to come. </p>
      <p>The Democratic National Committee and several people involved in Hillary Clinton's presidential campaign became the target of a major hack-and-leak operation. Later, investigations uncovered an extensive campaign, targeting not just political figures but also state election infrastructure. The hack has since been attributed by U.S. intelligence to Russian state actors.</p>
      <LottieAnim
      playAnim={true}
      looped={true}
      data={mshadow}/>
      <p>These events cast a long shadow over the U.S. political community; cybersecurity became a major topic of concern.</p>
      <p>No one wanted to see history repeated.</p>
      <p>Ginny Badanes, Director of Strategic Projects, Cybersecurity and Democracy, Microsoft, experienced this first-hand: “The conversations customers wanted to have with us changed from using data visualization and machine learning to the importance of things like multi-factor authentication.”</p>
      <p>Although the 2020 elections weren’t hit by a repeat of a major hacking incident, there were still moments of significant tension, when prompt action helped avoid problems.</p>
  </main>
)

export default MHistory
