import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import './NetworkTitle.css'
const NetworkTitle =()=>{
    const state = useContext(DemocracyStateContext)
    const { currentStory } = state
  return(
    <section className="NetworkTitle">
       <article className="bigL">
         <h2 style={{"transform":"translate3d(-"+(currentStory === 1 ? 0 : 40)+"vw,0,0)"}}>
          Creating the
          network, one
          connection
          at a time
         </h2>
       </article>
    </section>
  )
}

export default NetworkTitle
